<template>
	<view class="content">		<view style="width:100%;text-align:center;position:fixed;color:#FFF;z-index:21;" :style="{'top':(top - h5H)+'px'}">视频收藏</view>
		<view class="content-head" :style="{ 'padding-top': top + 'px' }" id="toHeadLine">
			<view :class="typeSet==1? 'ok':''" @click="setClass(1)">全部</view>
			<view :class="typeSet==2? 'ok':''" @click="setClass(2)">免费</view>
			<view :class="typeSet==3? 'ok':''" @click="setClass(3)">收费</view>
		</view>
		<view :style="{'margin-top':contentTop+'px'}">
			<view class="video" v-for="(v, i) in collectList" :key="i">
				<view class="videoimg" @click="playVideo(v.videoId)" :style="{ background: 'url('+ bg +')' }">
					<image :src="v.videoImgUrl" mode="aspectFill"></image>
				</view>
				<view class="video-play-time">{{v.play_time}}</view>
				<view class="video-title">{{v.videoTitle}}</view>
				<view class="video-bot">
					<view class="video-gold">
						<image class="gold" src="@/static/imgs/gold2.png" mode="aspectFill"></image>{{v.gold}}
					</view>
					<image @click="delCollect(v.videoId)" class="dels" src="@/static/imgs/del.png" mode="aspectFill"></image>
				</view>
			</view>
		</view>
		<view style="width:100%;height:500upx;margin-top:600upx;text-align:center;" v-if="isShowTig">
			<image src="@/static/imgs/nodata.png" style="width:280upx;height:180upx;"></image>
			<view style="width:100%;font-size:25upx;color:#666;margin-top:30upx;">暂无视频，快去收藏喜欢的视频吧</view>
		</view>
	</view>
</template>

<script>
import api from "@/common/api.js";
var _self;
export default {
	data() {
		return {
			bg: '',
			top: 27,
			userId: 0,
			collectList: {},
			isShowTig: false,
			type: 0,
			typeSet: 1,
			contentTop: 107,
			h5H: 43,
		}
	},
	onLoad() {
		_self = this;
		var mobile = uni.getSystemInfoSync();
		_self.top = mobile.statusBarHeight+53;
		//console.log(mobile)
		// 是否登录
		var info = api.getLogins();
		if(info === false) {
			uni.showLoading({ title:'加载中...',mask:true });
			_self.skipUrl();
		}else{
			_self.userId = info.userId;
			_self.getCollect();
		}
	},
	mounted: function() {
		const query = uni.createSelectorQuery().in(this);
		query.select('#toHeadLine').boundingClientRect(data => {
			_self.contentTop = data.top+data.height+15;
			//console.log(_self.contentTop)
		}).exec();		
	},
	methods: {
		// 切换TAB
		setClass(type) {
			//console.log(type)
			_self.typeSet = type;
			_self.getCollect();
		},
		// 收藏列表 
		getCollect() {
			uni.showLoading({ title:'加载中...',mask:true });
			uni.request({
				url: api.apiData.getColl+'/userId/'+_self.userId+'/type/'+_self.typeSet,
				method: 'GET',
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					uni.hideLoading();
					//console.log(_self.typeSet);
					_self.collectList = e.data.Data;
					if(_self.collectList.length>0){
						_self.isShowTig = false;
					}else{
						_self.isShowTig = true;
					}
				},
				fail: () => {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 跳转播放页
		playVideo(vid) {
			uni.showLoading({ title:'加载中...',mask:true });
			//console.log(vid);
			uni.setStorage({
			    key: 'vid',
			    data: vid,
			    fail: ()=> {
			        _self.msgData('服务器连接失败，请检查网络是否正常', 5000);
					return false;
			    },
				complete: () => {
					var param = {
						url: '/pages/member/collect',
						t: 'new'
					}
					api.jumpUrl('/pages/video/play?type='+JSON.stringify(param), 'new');
				}
			});
		},
		// 删除收藏视频 
		delCollect(vid){
			uni.showModal({
			    title: '提示',
			    content: '确认要删除收藏吗？',
				cancelText: '点错了',
				confirmText: '确定删除',
			    success: (res)=> {
			        if (res.confirm) {
			            //console.log('用户点击确定');
						_self.delCollectVideo(vid);
			        } else if (res.cancel) {
			            //console.log('用户点击取消');
			        }
			    }
			});
			//console.log(vid)
		},
		// 删除操作
		delCollectVideo(vid) {
			uni.showLoading({ title: '处理中...' });
			uni.request({
				url: api.apiData.delColl,
				method: 'POST',
				data: { 
					userId: _self.userId,
					videoId: vid
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					//console.log(vid)
					uni.hideLoading();
					var res = e.data;
					_self.msgData(res.Msg);
					if(res.Code == 200) _self.getCollect();
				},
				fail: () => {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		// 跳转登录
		skipUrl(url = '/pages/login/login', type = 1) {
			if(type == 1){
				uni.reLaunch({ url: url });
			}else{
				uni.navigateTo({ url: url });
			}
		},
		// 提示
		msgData(title = '', times = 1000, style = 'none') {
			// 加载失败提示
			uni.showToast({
				icon: style,
				title: title,
				mask: true,
				duration: times
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		_self.getCollect();
		uni.stopPullDownRefresh();
	},
	onBackPress(e) {
		api.jumpUrl('/pages/member/member', 'tab');
		return true;
	}
}
</script>

<style>
.video-play-time{
	position: relative;
	float: right;
	margin-top: -25px;
	background: -webkit-gradient(linear, left top, right top, from(rgba(0,0,0,0.6)), to(#ff6f33));
	padding: 5upx 10upx;
	height: 30upx;
	line-height: 30upx;
	border-radius: 3px 0 0 3px;
	color: #FFFFFF;
	font-size: 23upx;
}
.video{
	background: #FFFFFF;
	width: 45%;
	display: inline-block;
	margin-left: 3.3%;
	border-radius: 10px;
	color: #000000;
	font-size: 15px;
	padding: 0 0 5px 0;
	box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.5);
	margin-bottom: 13px;
	z-index: 19;
	position: relative;
}
.video-title{ 
	margin-top: 3px;
	padding: 0 6px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	width: 93%;
	line-height: 40upx;
	font-size: 14px;
}
.video-gold{
	//margin-left: 2%;
	padding: 0 10upx;
	height: 13px;
	line-height: 13px;
	border-radius: 5upx;
	//background: #fff0e2;
	color: #ff9030;
	font-size: 13px;
}
.video-bot{
	padding: 0 6px;
	margin-top: 5px;
	//line-height: 12px;
	font-size: 14px;
}
.video-bot view{ display: inline-block; }
.video-bot image{
	width: 16px;
	height: 16px;
}
.video-bot .gold{
	float: left;
	width: 13px;
	height: 13px;
	margin-right: 3px;
}
.video-bot .dels{
	float: right;
}
.videoimg{ height: 80px; }
.videoimg image{
	border-radius: 10px 10px 0 0;
	width: 100%;
	height: 80px;
	background: #24292E;
}
.image{
	width: 100%;
	height: 233px;
	position: fixed;
	top: 0;
	left: 0;
}
.ok{ 
	//border-bottom: 2px solid #FFFFFF; 
	//color: #FF3637;
	background-color: rgba(0,0,0,0.2);
	border-radius: 50upx;
}
.content-head{ 
	position: fixed;
	width: 100%;
	height: 30px;
	line-height: 30px;
	padding: 10upx 0 20upx;
	background-image: linear-gradient(to right,rgba(255,12,208,1), rgba(254,67,101,1));
	//border-bottom: 2px solid rgba(255,12,208,0.5);
	top: 0;
	text-align: center;
	color: #FFFFFF; 
	z-index: 20;
	//background-image:linear-gradient(to right,rgba(239,187,52,0.5), rgba(255,255,255,0.1));text-align: center;
}
.content-head view{ display: inline-block;width: 23%;height: 30px; }
</style> 
